/*    */
.overlay {

    /*    */
    opacity: 0;
    visibility: hidden;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: getColor(modal-overlay);
    z-index: 20;
    transition: .3s all;
}

/*     */

.modal {

    /*    */
    opacity: 0;
    visibility: hidden;


    /*      */
    width: 100%;
    max-width: 500px;
    max-height: 99vh;

    /*     z-index */
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
    /*      */

    /*      */
    box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
    text-align: center;
    padding: 30px;
    border-radius: 3px;
    background-color: #fff;
    transition: 0.3s all;

    overflow-y: auto;
}


/*        */

.modal.active,
.overlay.active {
    opacity: 1;
    visibility: visible;
}