  .spinner {
    position: absolute;
    top: 50%;
    left: 50%;

    display: inline-block;
    width: 100px;
    height: 100px;

    transform: translate(-50%, -50%);
  }

  .spinner div {
    position: absolute;
    box-sizing: border-box;

    display: block;
    margin: 8px;
    width: 75px;
    height: 75px;

    border: 8px solid getColor(accent-text-color);
    border-radius: 50%;
    border-color: getColor(accent-text-color) transparent transparent transparent;

    animation: spinner 1400ms cubic-bezier(0.5, 0, 0.5, 1) infinite;
  }

  .spinner div:nth-child(1) {
    animation-delay: -450ms;
  }

  .spinner div:nth-child(2) {
    animation-delay: -300ms;
  }

  .spinner div:nth-child(3) {
    animation-delay: -150ms;
  }

  @keyframes spinner {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }